@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Andada+Pro:wght@400;500;600;700;800&display=swap);
.header {
  width: 100%;
  height: 80px;
  line-height: 80px;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  grid-column-gap: 1.3rem;
  column-gap: 1.3rem;
}

.nav__item a {
  font-weight: 500;
  font-size: 1.1rem;
  color: #0a2b1e;
  text-decoration: none;
  transition: 0.3s;
}

.nav__item a:hover {
  color: orangered;
}

.nav__right p {
  font-weight: 500;
  color: #232323;
  font-size: 1.1rem;
}

.nav__right p i {
  font-size: 1.3rem;
}

.mobile__menu span i {
  font-size: 1.4rem;
  color: rgb(37, 38, 38);
  cursor: pointer;
}

.mobile__menu {
  display: none;
}

@media only screen and (max-width: 991px) {
  .nav__menu {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #0a2b1ec2;
    z-index: 99;
    display: none;
  }

  .nav__list {
    position: absolute;
    background: #fff;
    top: 0;
    right: 0;
    width: 250px;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 999;
    padding-top: 80px;
  }

  .mobile__menu {
    display: block;
  }
  .nav__right {
    display: none;
  }

  .active__menu {
    display: block;
  }
}

.hero__content h2 {
  font-weight: 400;
  line-height: 45px;
}

.hero__content {
  padding-top: 80px;
}
.search input {
  border: none;
  outline: none;
  padding: 7px 10%;
}

.search {
  width: 60%;
  border: 1px solid rgba(22, 22, 22, 0.267);
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 50px;
  margin-top: 20px;
}

.about__content {
  text-align: center;
}

.rounded-card {
  width: 200px; /* Adjust the overall width of the card */
  height: 300px; /* Adjust the overall height of the card */
  border-radius: 15px; /* Adjust the border radius */
  overflow: hidden;
  margin-bottom: 20px;
  box-shadow: 15px 10px 10px rgba(0, 0, 0, 0.1);
  position: relative;
  transition: box-shadow 0.3s, transform 0.3s;
}

.rounded-card:hover {
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  transform: translateY(-4px);
}

.rounded-img {
  border-radius: 15px 15px 0 0; /* Adjust the border radius for the image */
  height: 50%; /* Adjust the height of the image */
  width: 100%;
  object-fit: cover;
  background: rgb(162, 161, 161);
}

.card-body {
  height: 50%; /* Adjust the height of the card body */
  background: rgb(248, 242, 242);
}

.card-body h2 {
  font-weight: 600;
  margin-top: 10px; /* Adjust the margin as needed */
}

/* Add any additional styling for your cards here */

.single__course__item {
  padding: 15px;
}

.course__img img {
  border-radius: 10px;
  margin-bottom: 20px;
}

.enroll a {
  text-decoration: none;
  font-weight: 600;
  font-size: 0.9rem;
  color: orangered;
}

.lesson,
.students,
.rating {
  font-size: 0.9rem;
  color: #2f3030;
  font-weight: 500;
}

.lesson i,
.students i,
.rating i {
  font-weight: 400;
}

.course__title {
  line-height: 2rem;
}

.course__top__left h2 {
  font-weight: 600;
}

.choose__img img {
  border-radius: 15px;
}

.choose__content {
  padding-right: 15px;
  padding-left: 0px;
}

.choose__content h2 {
  font-weight: 600;
}

.choose__img {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 777;
}

.play__icon {
  position: absolute;
  top: 35%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #323232;
  z-index: 7777;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.play__icon i {
  color: orangered;
  font-size: 2rem;
  padding: 10px;
}

.single__feature h2 i {
  font-size: 2.5rem;
}

.single__feature h6 {
  font-weight: 600;
}

.testimonial__content h2 {
  font-weight: 600;
}

.newsletter {
  background: #17bf9e;

  padding: 60px;
  border-radius: 15px;
}

.newsletter h2 {
  color: #fff;
}
.subscribe input {
  border: none;
  outline: none;
}

.subscribe {
  padding: 7px 10px;
  background: #fff;
  width: 45%;
  margin: auto;
  border-radius: 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.footer {
  background: rgb(251, 242, 238);
  text-align: center;
}

.follows span a {
  text-decoration: none;
}

.follows span {
  margin-right: 15px;
}

.link__list .link__item {
  background: transparent;
}

.link__list a {
  text-decoration: none;
  color: #131212;
  font-weight: 500;
}
.link__item a:hover {
  color: orangered;
}

/* =========== base style ========= */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

section,
footer {
  padding-top: 70px;
  padding-bottom: 70px;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 2rem;
  line-height: 50px;
  font-family: "Poppins", sans-serif;
  color: #151515;
  font-weight: 600;
}

p {
  color: #3d3a3a;
  font-size: 1rem;
  line-height: 35px;
  font-family: "Andada Pro", serif;
  font-weight: 400;
}

button.btn {
  background: rgb(248, 90, 32);
  color: #fff;
  padding: 7px 25px;
  border-radius: 50px;
}

i {
  color: orangered;
}

